<template>
  <div class="DEVBLOGS">
    <!-- Headers -->
    <div class="text-center m-5">
      <h1 class="my-3">DEV BLOGS</h1>
      <h4>I learn as I go</h4>
    </div>

    <!-- Posts -->
    <div v-for="post in getPageContent(pagination.page)" :key="post.id" class="mt-5">
      <Post :post="post" data-aos="fade-up"/>
    </div>

    <!-- Pagination -->
    <div class="text-center my-3">
      <v-pagination
        v-model="pagination.page"
        :circle="pagination.circle"
        :length="pagination.length"
        :next-icon="pagination.nextIcon"
        :prev-icon="pagination.prevIcon"
        :page="pagination.page"
        :total-visible="pagination.totalVisible"
      ></v-pagination>
    </div>
  </div>
</template>

<script>
import Post from "@/components/Post.vue";
import posts from "@/assets/models/posts.json";

let postPerPage = 4;
export default {
  name: "Home",
  components: {
    Post
  },
  data() {
    return {
      posts,
      pagination: {
        circle: false,
        length: Math.ceil(posts.length / postPerPage),
        nextIcon: "fas fa-angle-right",
        prevIcon: "fas fa-angle-left",
        page: 1,
        totalVisible: 10
      }
    };
  },
  methods: {
    getPageContent: function(page) {
      // sort posts
      function compare(a, b) {
        var d1 = new Date(a.date);
        var d2 = new Date(b.date);
        if (d1 < d2) return 1;
        if (d1 > d2) return -1;
        return 0;
      }
      let sortedPosts = posts.sort(compare);

      // return list of posts
      let startPage = (this.pagination.page - 1) * postPerPage;
      let endPage = startPage + postPerPage;
      return sortedPosts.slice(startPage, endPage);
    }
  }
};
</script>

<style>
  .DEVBLOGS h1 {
    font-family: 'PT Sans Narrow', sans-serif;
  }
  .DEVBLOGS h4 {
    font-family: 'PT Sans Narrow', sans-serif;
  }
</style>