<template>
  <div class="POST">
    <v-container>
      <div v-for="post in current_post" :key="post.id" class="my-5">
        <v-card>
          <!-- Title -->
          <v-card-title primary-title>
            <div class="mt-4 mb-3 mx-2">
              <h1>{{ post.title }}</h1>
              <h6>
                <v-icon size="18px" class="mr-2">fas fa-clock</v-icon>
                {{ generate_date_string(post.date) }}
              </h6>
            </div>
          </v-card-title>

          <!-- Image -->
          <img v-if="post.image" :src="require('@/assets/images/' + post.image)" />

          <!-- Content -->
          <v-card-text class="mx-2">
            <div
              v-for="(paragraph, index) in generate_paragraphs(post.content)"
              :key="`${index}`"
              class="w-100"
            >
              <p>{{ remove_image_tags(paragraph) }}</p>
              <v-flex
                :class="'sm'+12/resolve_images(paragraph).length"
                v-for="img_path in resolve_images(paragraph)"
                :key="img_path"
                class="p-1 mx-auto d-inline-block w-100"
              >
                <img :src="require('@/assets/images/' + img_path)" class="mx-auto" />
              </v-flex>
            </div>
          </v-card-text>

          <v-divider class="mx-4"/>

          <v-card-text v-if="post.links" class="mx-2">
            <div v-for="(link, index) in post.links" :key="link">
              <p>[{{index+1}}] {{link[0]}} - <a class="text-break" :href="link[1]">{{link[1]}}</a></p>
            </div>
          </v-card-text>
        </v-card>
      </div>
    </v-container>
  </div>
</template>

<script>
import posts from "@/assets/models/posts.json";
export default {
  name: "Post",
  props: {
    id: String
  },
  data() {
    return {
      posts
    };
  },
  computed: {
    current_post() {
      return posts.filter(p => {
        return p.id == this.id;
      });
    }
  },
  methods: {
    generate_paragraphs: function(text) {
      return text.split("\n");
    },
    generate_date_string: function(jsonStr) {
      var date = new Date(jsonStr);
      var day = date.getDate();
      var month = date.toLocaleString("en-us", { month: "long" });
      var year = date.getFullYear();
      return month + " " + day + ", " + year;
    },
    resolve_images(paragraph) {
      let regexp = /\[\[(.*?)\]\]/g;
      var m;
      var output = [];
      do {
        m = regexp.exec(paragraph);
        if (m) {
          output.push(m[1]);
        }
      } while (m);
      return output;
    },
    remove_image_tags(paragraph) {
      return paragraph.replace(/\[\[(.*?)\]\]/g, "");
    }
  }
};
</script>

