<template>
  <div class="HOME">
    <!-- Banner -->
    <v-parallax :src="require('@/assets/images/951.png')">
      <v-layout column justify-center class="ml-4">
        <div data-aos="fade-right" data-aos-duration="1000">
          <h1 class="display-2 mb-3">&gt; FATİH KAÇAR</h1>
          <h3>Full-Stack Developer _</h3>
        </div>
      </v-layout>
    </v-parallax>
    <!-- Recent Posts -->
    <div class="POSTS">
      <div class="text-center mt-5">
        <v-icon>fac fa-clipboard</v-icon>
        <h1 class="my-3">RECENT POSTS</h1>
      </div>
      <div v-for="post in sortedPosts" :key="post.id">
        <Post :post="post" data-aos="fade-up"/>
      </div>
    </div>
  </div>
</template>

<script>
import Post from "@/components/Post.vue";
import posts from "@/assets/models/posts.json";

export default {
  name: "Home",
  components: {
    Post
  },
  data() {
    return {
      posts
    };
  },
  computed: {
    sortedPosts: function() {
      function compare(a, b) {
        var d1 = new Date(a.date);
        var d2 = new Date(b.date);
        if (d1 < d2) return 1;
        if (d1 > d2) return -1;
        return 0;
      }
      return posts.sort(compare).slice(0, 3);
    }
  }
};
</script>

