import Vue from 'vue'
import Router from 'vue-router'

import Home from './views/Home.vue'
import About from './views/About.vue'
import Projects from './views/Projects.vue'
import DevBlogs from './views/DevBlogs.vue'
import Contact from './views/Contact.vue'
import Post from './views/Post.vue'
import Project from './views/Project.vue'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home,
    },
    {
      path: '/about',
      name: 'about',
      component: About
    },
    {
      path: '/projects',
      name: 'projects',
      component: Projects
    },
    {
      path: '/dev-blogs',
      name: 'dev-blogs',
      component: DevBlogs
    },
    {
      path: '/contact',
      name: 'contact',
      component: Contact
    },
    {
      path: '/post/:id',
      name: 'post',
      component: Post,
      props: true
    },
    {
      path: '/projects/:id',
      name: 'project',
      component: Project
    }
  ]
})
