<template>
  <div class="PROJECT">
    <v-container fill-height>
      <div class="w-100">
        <v-layout row wrap align-center class="my-5 py-4 w-100">
          <v-flex xs12 sm6 class="text-center">
            <h1>{{ project.title.toUpperCase() }}</h1>
            <h4>{{ project.duration }}</h4>
          </v-flex>
          <v-flex xs12 sm6>
            <img :src="require('@/assets/images/' + project.image[0])" />
          </v-flex>
        </v-layout>

        <!-- Description -->
        <div class="my-5">
          <h1 class="mb-4 subtitle">DESCRIPTION</h1>
          <p
            v-for="(paragraph, index) in generate_paragraphs(project.description)"
            :key="`${index}`"
          >{{ paragraph }}</p>
        </div>

        <!-- Gallery -->
        <h1 class="mb-4 subtitle">GALLERY</h1>
        <div v-if="project.video">
          <!-- local file -->
          <vue-plyr>
            <video v-if="project.video.local">
              <source :src="require('@/assets/videos/'+project.video.local)" type="video/mp4" />
            </video>
          </vue-plyr>
          <!-- youtube link -->
          <vue-plyr v-if="project.video.youtube">
            <div data-plyr-provider="youtube" :data-plyr-embed-id="project.video.youtube"></div>
          </vue-plyr>
        </div>
        <div id="LightGallery" class="my-4">
          <LightGallery
            :images="absolutePaths(project.image)"
            :index="index"
            :disable-scroll="true"
            @close="index = null"
          />
          <ul>
            <li
              v-for="(thumb, thumbIndex) in absolutePaths(project.image)"
              :key="thumbIndex"
              :style="(index) ? '' : 'display: inline'"
              @click="index = thumbIndex"
            >
              <img class="thumb" :src="thumb" :style="(index) ? '' : 'max-width: 120px'" />
            </li>
          </ul>
        </div>

        <!-- About the Project -->
        <div v-if="project.about" class="my-5">
          <h1 class="mb-4 subtitle">ABOUT THE PROJECT</h1>
          <p v-for="(value, key) in project.about" :key="`${key}`"><u>{{ key }}:</u> {{ value }}</p>
          <div v-if="project.features">
            <u>Main Features:</u>
            <ul>
              <li v-for="(features, category) in project.features" :key="category">
                {{category}}
                <ul>
                  <li v-for="feature in features" :key="feature">{{feature}}</li>
                </ul>
              </li>
            </ul>
          </div>
          <div v-if="project.responsibilities">
            <u>Responsibilities:</u>
            <ul>
              <li
                v-for="responsibility in project.responsibilities"
                :key="responsibility"
              >{{ responsibility }}</li>
            </ul>
          </div>
        </div>

        <!-- Members -->
        <div v-if="project.members" class="my-5">
          <h1 class="mb-4 subtitle">MEMBERS</h1>
          <div v-for="member in project.members" :key="member" class="member">
            <div @click="openNewTab(people[member].url)">
              <v-avatar
                size="4rem"
                color="grey lighten-4"
                data-aos="fade"
                data-aos-duration="1000"
                class="float-left mr-3"
              >
                <img :src="people[member].avatar" alt="avatar" />
              </v-avatar>
              <h4>{{ people[member].name }}</h4>
              <p>{{ people[member].description }}</p>
            </div>
          </div>
        </div>

        <!-- Useful Links -->
        <div v-if="project.links" class="my-5">
          <h1 class="mb-4 subtitle">USEFUL LINKS</h1>
          <div v-for="(link, index) in project.links" :key="index" class="mb-3">
            <h4>{{ link.name }}</h4>
            <a :href="link.url">{{ link.url }}</a>
          </div>
        </div>
      </div>
    </v-container>
  </div>
</template>

<script>
import projects from "@/assets/models/projects.json";
import people from "@/assets/models/people.json";
import { LightGallery } from "vue-light-gallery";
export default {
  name: "Project",
  components: {
    LightGallery
  },
  data() {
    return {
      id: this.$route.params.id,
      people,
      index: null
    };
  },
  computed: {
    project() {
      return projects.filter(p => {
        return p.id == this.id;
      })[0];
    }
  },
  methods: {
    generate_paragraphs: function(text) {
      return text.split("\n");
    },
    openNewTab: function(url) {
      window.open(url);
    },
    absolutePaths: function(arr) {
      return arr.map(x => require("@/assets/images/" + x));
    }
  }
};
</script>

<style>
div.PROJECT img {
  width: 100%;
}
div.PROJECT a {
  color: #cdb8d3;
  word-break: break-all;
}
div.PROJECT a:hover {
  color: #89798f;
  text-decoration: none;
}
div.member:hover {
  color: #9c9ba0;
  cursor: pointer;
}
.PROJECT h1 {
  font-family: 'PT Sans Narrow', sans-serif;
}
.PROJECT h4 {
  font-family: 'PT Sans Narrow', sans-serif;
  color: #7c7c7c !important;
}
.PROJECT p {
  font-family: 'Baloo Bhai 2', cursive;
}
.PROJECT .subtitle {
  color: rgba(0, 0, 0, 0.5) !important;
}
div#LightGallery ul {
  list-style-type: none;
}
img.thumb {
  transition: box-shadow 0.2s ease-in-out;
  transition: transform 0.2s ease-in-out;
  cursor: pointer;
  margin: 10px;
}
img.thumb:hover {
  box-shadow: 0 0 20px 4px rgba(112, 100, 155, 0.911);
  transform: scale(1.05);
}
video {
  width: 100%;
}
</style>
