<template>
  <div class="post">
    <b-card bg-variant="dark" no-body class="overflow-hidden mx-auto" style="max-width: 800px;">
      <router-link tag="div" :to="'/post/' + post.id">
        <b-row no-gutters class="post-card">
          <b-col md="6" v-if="hasImage">
            <b-card-img :src="require('@/assets/images/' + post.image)" class="rounded-0"></b-card-img>
          </b-col>
          <b-col :md="hasImage? 6 : 12">
            <b-card-body :title="post.title">
              <h6>{{ generate_date_string(post.date) }}</h6>
              <div class="text-fade-mask"></div>
              <b-card-text class="mt-5 pb-2">
                <p
                  v-for="(paragraph, index) in generate_paragraphs(post.content)"
                  :key="`${index}`"
                >{{ paragraph }}</p>
              </b-card-text>
            </b-card-body>
          </b-col>
        </b-row>
      </router-link>
    </b-card>
  </div>
</template>

<script>
export default {
  name: "Post",
  props: ["post"],
  data() {
    return {
      //
    };
  },
  computed: {
    hasImage: function() {
      return this.post.image != undefined;
    }
  },
  methods: {
    generate_paragraphs: function(text) {
      return text.split("\n");
    },
    generate_date_string: function(jsonStr) {
      var date = new Date(jsonStr);
      var day = date.getDate();
      var month = date.toLocaleString("en-us", { month: "long" });
      var year = date.getFullYear();
      return month + " " + day + ", " + year;
    }
  }
};
</script>

